.square {
  border: 1px solid #999;
  width: 100px;
  height: 100px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2.6rem;
  text-transform: uppercase;
}

.square:hover {
  background-color: whitesmoke;
}

.green_text {
  color: #53af50;
}
